/* Estilo para el contenedor del footer en dispositivos web */
.footer {
  background-color: #dbbeff;
  color: #111;
  padding: 20px;
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 30px;
}

/* Estilo para el contenedor del footer en dispositivos móviles */
@media (max-width: 768px) {
  .footer {
    flex-direction: column;
    justify-content: center;
    padding-top: 10%;
  }
}

/* Estilo para las secciones del footer en dispositivos web */
.footer-section {
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: left;
  width: 20%;
  margin-bottom: 20px;
}

/* Estilo para las secciones del footer en dispositivos móviles */
@media (max-width: 768px) {
  .footer-section {
    width: 100%;
    align-items: center;
  }
}

h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

p {
  margin: 5px 0;
}

/* Estilo para los nombres de desarrolladores */
.developer-name {
  font-weight: bold;
}

/* Estilo para el nombre del diseñador */
.designer-name {
  font-weight: bold;
}
