.ebook {
    width: 100%;
    height: fit-content;
    padding: 2%;
    background-color: #D5FF53;
    color: #111;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .ebook-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .ebook-title {
    font-size: 3rem;
    font-weight: 900;
    margin: 0.1rem 0;    -webkit-text-stroke: 1px;
  }
  
  .ebook-text {
    font-size: 1.5rem;
    font-weight: 900;
    margin: 0.1rem 0;
  }
  
  .ebook-button {
    display: flex;
    align-items: center;
    width: 100%;
    height: 3rem;
    background-color: #111;
    color: #FFF;
    border: none;
    border-radius: 5px;
    font-size: 1.5rem;
    font-weight: 900;
    margin: 0.5rem 0;
    padding: 1rem;
    cursor: pointer;
  }
  
  @media screen and (max-width: 768px) {
    .ebook {
      padding: 5%;
    }
  }
  
  @media screen and (max-width: 480px) {
    .ebook {
      padding: 10%;
    }
  }
  