.charlas {
  margin: 0;
  box-sizing: border-box;
  background-color: #eee;
  display: flex;
  flex-direction: column;
  padding: 3% 5%;
  scroll-margin-top: 80px;
}

.charlas-title {
  text-align: center;
  font-size: 3rem;
  font-weight: 900;
  margin: 7px 0;
  color: #ca7fec;
  width: 100%;
  -webkit-text-stroke: 1px;
}

.charlas-imgs {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.charlas-img {
  width: 33%;
}

.charlas-description {
  font-size: 2rem;
  margin: 7px 0;
}

.charlas-description a {
  text-decoration: none;
  color: #007bff;
}

.charlas-description a:hover {
  text-decoration: underline;
  color: #0056b3;
}

.video-responsive {
  position: relative;
  padding-bottom: 56.25%;
  margin-top: 20px;
}

.charlas-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/*mobile view*/

@media (max-width: 768px) {
  .charlas-img {
    width: 100%;
  }

  .charlas-description {
    font-size: 1.5rem;
  }

}