.contact {
  width: 100%;
  display: flex;
  background-color: #DABDFF;
  height: fit-content;
  padding-top: 4rem;
}

.contact-left {
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5%;
}

.contact-flower {
  width: 100%;
}

.contact-left h3 {
  color: #111;
  font-size: 3rem;
  font-weight: 900;
  -webkit-text-stroke: 1px;
}

.social-links {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
  flex-wrap: wrap;
}

.social-link {
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding: 1%;
  margin-left: 10PX;
}

.social-link img {
  width: 80px;
  height: 80px;
  margin-right: 1px;
}

.social-link a {
  text-decoration: none;
  color: inherit;
  font-weight: 900;
}

.social-link p {
  font-size: 1.5rem;
  font-weight: 900;
  color: #111;
  margin: 0;
}

.social-link p:hover {
  font-size: 1.6rem;
}

.contact-right {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form-aviso {
  font-weight: 900;
  font-size: 1.5rem;
}

.text-area {
  width: 100%;
  height: 200px;
  resize: none;
  border-radius: 10px;
  border: .5px solid #111;
  padding: 1%;
  font-size: 1.2rem;
  font-weight: 300;
  margin-bottom: 2%;
}

@media screen and (max-width: 800px) {
  .contact {
    flex-direction: column;
    padding-bottom: 4%;
  }

  .contact-left {
    width: 100%;
    margin: 0;
    padding: 0;
  }

  .contact-flower {
    display: none;
  }

  .contact-right {
    width: 100%;
    padding-bottom: 10%;
  }

  .social-links {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    padding: 0;
    margin: 0;
  }

  .social-link {
    width: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }

  .social-link img {
    width: 50px;
    height: 50px;
    margin-right: 1px;
  }
}
