.explicacion {
    text-align: center;
    padding: 20px;
}

.explicacion span {
    font-size: 3rem;
    font-weight: bold;
    -webkit-text-stroke: 1px;
}

.servicios {
    width: 100%;
    padding: top 20px;
    scroll-margin-top: 80px;
}

.servs {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch; /* Añade esta propiedad para igualar la altura */
}

.redondeo {
    display: block !important;
    flex-direction: column;
    align-items: center;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    text-align: center;
    width: 25%;
    padding: 7% 2%;
}

.arrow {
    width: 20px;
}

.servicios-button {
    background-color: #fff;
    border: 3px solid #fff;
    border-radius: 15px;
    color: #111;
    font-size: 18px;
    font-weight: 700;
    height: 50px;
    margin-top: 3%;
    text-decoration: none;
    width: 150px;
}

.custom-button {
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    margin-top: 10px;
    transition: background-color 0.3s, color 0.3s;
    border-radius: 10px;
    background-color: #ffffff80;
}

.custom-button a {
    text-decoration: none;
    color: inherit;
    font-size: 1rem;
    font-weight: 900;
}

.service-title {
    font-size: 2rem;
    font-weight: 1000;
    margin: 7px 0;
    width: 100%;
    text-align: center;
    flex: 1;
}

.service-description {
    font-weight: 900;
    font-size: 1.5rem;
    margin: 7px 0 20px;
    width: 100%;
    text-align: center;
}

@media screen and (max-width: 1024px) {
    .servicios {
        flex-direction: column;
    }

    .redondeo {
        width: 100%;
        border-radius: 0;
    }
}
