.hola {
    background-color: #eee;
    color: #000;
    width: 100%;
    padding: 10px 0 0 0;
    display: flex;
}

.hola-container1 {
    width: 55%;
    padding-left: 10%;
    padding-top: 50px;
}

.hola-container2 {
    width: 45%;
}

.hola-title {
    color: #CA7FFE;
    width: 100%;
    font-weight: 1100;
    font-size: 3rem;
    -webkit-text-stroke: 2px #CA7FFE;
}

.hola-text {
    padding: 25px 0;
    font-size: 1.5rem;
    font-weight: 500;
}

.hola-negrita {
    font-weight:  900;
    background-color: #DBBEFF;
    padding: 1px 3px;
    border-radius: 7px;
}

.hola-img {
    max-height: 75vh;
}

@media screen and (max-width: 768px) {
    .hola {
        flex-direction: column-reverse;
        padding-right: 2%;
        padding-left: 2%;
    }

    .hola-container1 {
        width: 100%;
        padding: 10%;
    }

    .hola-container2 {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        background-color: #DBBEFF;
        border-radius: 300px 300px 0 0;
        padding-top: 20px;
    }

    .hola-img {
        width: 90%;
    }

    .hola-title {
        font-size: 2.5rem;
    }

    .hola-subtitle {
        font-size: 1.3rem;
    }

    .hola-text {
        padding: 0;
        font-size: 1.2rem;
    }
}