/* Estilos generales */
.form-container {
  width: 80%;
}

.form-container input {
  width: 100%;
  padding: 1%;
  margin: 1%;
  border-radius: 5px;
  font-size: 1.2rem;
}

.contact-container {
  display: flex;
  padding-top: 1%;
  background-color: #dbbeff;
}

.contact-left {
  width: 50%;
  padding-left: 10%;
}

.contact-left img {
  width: 100%;
}

.contact-left h3 {
  font-size: 45px;
  margin-bottom: 15px;
  margin-left: 15px;
}

.social-links {
  display: flex;
}

.social-links a {
  text-decoration: none;
  color: white;
  margin-top: 3%;
}

.social-links img {
  width: 80px;
  height: 80px;
  border-radius: 100%;
  margin: 5px;
}

.social-links p {
  width: auto;
  margin-top: 3%;
  font-size: 25px;
  font-weight: 600;
  color: black;
}

.contact-form {
  width: 100%;
  padding: 1%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.servicios-form {
  width: 100%;
  padding: 1%;
  display: flex;
  flex-direction: column;
}

.servicios {
  width: 100%;
}

.checkbox-section {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 2% 0%;
}

.button-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button-container button {
  width: 50%;
  padding: 1%;
  margin: 1%;
  border-radius: 5px;
  font-size: 1.2rem;
  background-color: #F26921;
  color: white;
  border: none;
}

/* Vista móvil */
@media only screen and (max-width: 768px) {
  .contact-container {
    display: flex;
    padding-top: 3%;
    background-color: #dbbeff;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .contact-left {
    width: 100%;
    margin: 5% 0%;
    text-align: center;
  }

  .contact-left img {
    width: 100%;
  }

  .social-links img {
    width: 70px;
    height: 70px;
    border-radius: 100%;
  }

  .social-links {
    display: flex;
    flex-direction: column;
    margin: 0% 10%;
  }

  .social-links a {
    width: 100%;
    margin-bottom: 10px;
  }
}

/* CSS para el formulario */
.form-section {
  margin-bottom: 1rem;
}

.form-label {
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.checkbox-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 1rem;
  background-color: white;
  padding: 1%;
  margin: 1%;
  border-radius: 5px;
  font-size: 1.2rem;
}

.checkbox-section {
  display: flex;
  align-items: center;
  margin: 0.5rem 0;
}

.checkbox-label {
  margin-left: 0.5rem;
  min-width: fit-content;
  font-size: 1rem;
  font-weight: 400;
  color: black;
}

.text-area {
  height: 100px;
  resize: vertical;
}

.text-area.form-input {
  width: 100%;
}

/* Estilos para el botón de envío */
.submit-button {
  width: 50%;
  padding: 1%;
  margin: 1%;
  border-radius: 5px;
  font-size: 1.2rem;
  background-color: #F26921;
  color: white;
  border: none;
}

.submit-button:hover {
  background-color: #F26921;
}

/* Estilos para los errores en los campos de entrada */
input[style*="border: 1px solid red"] {
  border: 1px solid red !important;
}

/* Estilos para los campos de entrada válidos */
input[style*="border: 1px solid green"] {
  border: 1px solid green !important;
}
